<template>
        <div class="cenaro">
            <b-button href="/#/mainpage/servicecenter/location" size="lg" variant="outline-primary">내 주변에서 찾아보기</b-button>
            <br><br>
            <b-button href="/#/mainpage/servicecenter/kwloc" size="lg" variant="outline-primary">키워드로 찾아보기</b-button>
        </div>
</template>
<script>
export default {
    // http통신을 위한 설치

  created() {
      this.$http.get('/users')
          .then((res) => {
              const user = res.data.user;
              if (user) this.user = user;
          })
          .catch((err) => {
              console.error(err);
          });
  }
}
</script>
<style>
a{color:black}
  .cenaro{
    position:fixed; 
    top:400px; 
    left:900px; 
    width:auto; 
    height:1000px;
    size: 150%;
  }
</style>
